@import url(https://fonts.googleapis.com/css?family=Electrolize|Titillium+Web);
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input[type=range] {
  -webkit-appearance: none;
  background: transparent;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: rgb(101, 101, 101);
  height: 18px;
  width: 18px;
  margin-top: -8px;
  border-radius: 99px;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 300px;
  height: 4px;
  background: #cbff8b;
}
.header {
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 16px 24px;
  border: 3px solid #444;
  margin-top: 24px;
}
.play-button {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  outline: none;
  margin: 0 24px 0;
  border: none;
  border-radius: 15px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.3);
  height: 64px;
  width: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #cbff8b; 
  background:rgb(130, 130, 130);
}

.play-button:focus {
  background-color: #bbb;
  border: 2px solid #999;
}

.play-button::-moz-focus-inner {
  border: 0;
}

.play-button:hover {
  background-color: #bbb;
}

.play-button:active {
  background-color: #cbff8b;
}

.refresh-button {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  outline: none;
  margin: 0 24px 0;
  border: none;
  border-radius: 15px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.3);
  height: 64px;
  width: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #cbff8b; 
  background:rgb(130, 130, 130);
}

.refresh-button:focus {
  background-color: #bbb;
  border: 2px solid #999;
}

.refresh-button::-moz-focus-inner {
  border: 0;
}

.refresh-button:hover {
  background-color: #bbb;
}

.refresh-button:active {
  background-color: #cbff8b;
}

.tempo-slider {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  padding: 28px 24px 4px;
}

.tempo-slider__title {
  padding: 0 0 5px 32px;
  font-size: 27px;
  font-family: 'Electrolize', sans-serif;
  width: 190px;
}

.tempo-slider__slider {
  width: 128px;
}

.step {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}

.step--cursor {
  background-color: #fcc;
}

.drum-rack {
  display: block;
  padding: 8px;
  grid-gap: 0;
  justify-items: start;
  align-items: start;
  justify-content: center;
  align-content: start;
}

.drum-rack__title {
  grid-column: 1 / -1;
  width: 100%;
  padding: 4px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #999;
  font-size: 13px;
}

.drum-rack__remove {
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  border: none;
  background-color: transparent;
  color: #999;
  cursor: pointer;
}

.drum-rack__remove:hover {
  color: #eee;
}

.drum-rack__preloader-container {
  width: 236px;
  height: 236px;
  display: flex;
  justify-content: center;
  align-items: center; 
}

.step__cover {
  width: 64px;
  height: 64px;
  background-color: #aaa;
  position: absolute;
  top: 0;
  border: 2px solid #222;
  border-radius: 10%;
  cursor: pointer;
}

.step__cover--cursor {
  background-color: #494c52;
}

.step__checkbox {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.step__label {
  width: 64px;
  height: 64px;
  position: absolute;
  top: 0;
  left: 0;
}

input[type='checkbox']:checked ~ div {
  background: #cbff8b;
  background-color: #cbff8b;
}

input[type='checkbox']:checked ~ div.step__cover--cursor {
  opacity: 0.3;
}

.drum-rack__controls {
  display: flex;
}

.pitch-slider, .volume-slider {
  flex-direction: row;
  align-items: left;
  text-align: left;
  padding: 4px 0px 4px;
  margin: 5px;
  flex: 0 1 auto;
}

.pitch-slider__title, .volume-slider__title {
  padding: 0 0 5px 32px;
  font-size: 24px;
  font-family: 'Electrolize', sans-serif;
  width: 190px;
}

.pitch-slider__slider, .volume-slider__slider {
  width: 128px;
}

.pitch-slider__title {
  padding: 0 0 5px 32px;
}

.volume-slider__title {
  padding: 0 0 5px 15px;
}

@media screen and (max-width: 640px) , screen and (max-width: 768px) {
  .step, .step__cover, .step__label, .step__checkbox {
    width: 24px;
    height: 24px;
  }
}
@media screen and (max-width: 375px), screen and (max-width: 414px) {
  .step, .step__cover, .step__label, .step__checkbox {
    width: 20px;
    height: 20px;
  }
  .volume-slider, .pitch-slider {
    width: 160px;
  }
  .drum-rack {
    width: 375px;
  }
}

@media screen and (max-width: 320px) {
  .step, .step__cover, .step__label, .step__checkbox {
    width: 16px;
    height: 16px;
  }
  .volume-slider, .pitch-slider {
    width: 140px;
  }
  .drum-rack {
    width: 320px;
  }
}


.add-sample__container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.add-sample {
  width: 12.5em;
  height: 12.5em;
  line-height: 11em;
  padding: 40px 0px 14px 0;
  margin: 0;
}

.drum-button:hover {
  background: black;
  color: #fff;
  cursor: pointer;
}

.drum-button:active {
  position: relative;
  top: 1px;
  background: #333;
}

.add-sample__new {
  width: 100%;
  height: 100%;
  border: 4px solid #cbff8b;
  border-radius: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.add-sample__button {
  appearance: none;
  border: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  border-radius: 20%;
  background-color: transparent;
  color: #cbff8b;
  width: 100%;
  height: 100%;
  outline: 0;
  cursor: pointer;
}

.add-sample__button:hover {
  background-color: #333;
}

.add-sample__button:active {
  background-color: #cbff8b;
  color: #222;
}

.add-sample__text {
  font-family: 'Electrolize', sans-serif;
  padding: 32px;
  font-size: 27px;
  line-height: 52px;
}
.footer {
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 16px 24px;
  border-top: 3px solid #444;
  margin-top: 24px;
}
.signle-sample {
  border-top: 1px solid #444;
  padding: 0;
  font-family: 'Titillium Web', sans-serif;
  font-size: 15px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 48px;
}

.signle-sample:hover {
  background-color: #555;
}

.signle-sample__button {
  display: block;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: #ccc;
  padding: 8px 8px;
}

.signle-sample__button:hover {
  color: #eee;
}

.signle-sample__name {
  height: 100%;
  width: 100%;
  text-align: left;
  cursor: pointer;
}



.sample-selector__list {
  overflow: hidden scroll;
  height: calc(100% - 58px);
  padding: 16px;
}

.sample-selector__title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 8px 16px;
  border-bottom: 3px solid #444;
}

.sample-selector__heading-text {
  display: block;
  font-family: 'Titillium Web', sans-serif;
  font-size: 15px;
  font-weight: normal;
  text-transform: uppercase;
  padding: 8px 0;
  margin: 0;
  color: #ccc;
}

.sample-selector__close {
  display: block;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  outline: none;
  border: none;
  background-color: transparent;
  color: #999;
  cursor: pointer;
  width: 32px;
  padding: 0;
}

.sample-selector__close::-moz-focus-inner {
  border: 0;
}

.sample-selector__close:hover {
  color: #eee;
}

.sample-selector__close:focus {
  color: #eee;
}

.sample-selector__close:active {
  color: #c43e17;
}

.sample-selector__tabs {
  display: flex;
  flex-direction: row;
  justify-content: stretch;
  margin-bottom: 8px;
}

.sample-selector__tab-button {
  flex-grow: 1;
  display: block;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  outline: none;
  border: none;
  background-color: transparent;
  color: #bbb;
  cursor: pointer;
  padding: 16px;
  background-color: #000;
}

.sample-selector__tab-button::-moz-focus-inner {
  border: 0;
}

.sample-selector__tab-button:hover {
  opacity: .8;
}


.sample-selector__tab-button:active {
  background-color: #c43e17;
}

.sample-selector__tab-button--active {
  background-color: #333;
  color: #bbb;
}

/* Palette
fa9d07 - light shades
c43e17 - light acent
 602d2a - main brand color
 27295f - dark accent
 112257 - dark shades
*/

.App {
  background-color: #1e1e21;
  color: #cbff8b;
  min-height: 100vh;
  min-width: 100vw;
  padding: 16px 0 16px;
  font-family: 'Titillium Web', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.controls {
  grid-column: 1 / -1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  padding: 32px 24px;
  border-bottom: 3px solid #444;
  margin-bottom: 24px;
}

.drum-cols {
  display: inline-block;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  width: intrinsic; /* Safari/WebKit */
  width: -moz-max-content; /* Firefox/Gecko */
  width: -webkit-max-content;
}

* {
  box-sizing: border-box;
}

a {
  color: #aaa;
  text-decoration: none;
  outline: 0;
}

a:hover {
  text-decoration: underline;
}

.buttons {
  grid-column: 1 / -1;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}
.button-container {
  display: inline-block;
  width: auto;
  height: auto;
}

@media screen and (max-width: 375px), screen and (max-width: 414px) {
  .controls {
    width: 375px;
    }
  }
  
@media screen and (max-width: 320px) {
    .controls {
      width: 320px;
    }
  }
